            //     this.baroption.xAxis[0].data = value[2]
            //     this.baroption.series[0].data = value[3]
            //     mychart.clear();
            //     mychart.setOption(this.baroption,true)
            // }
            var _this = this
            $.ajax({
                type: "post",
                url: _this.baseUrl+"/ydjxzg/hx/xsgl.action",
                data: {reg:'jssltbdjt',style:'1'},
                dataType: "json",
                success: function (res) {
                    console.log(res)
                    if(res.jssltbdjt){
                        _this.xData = []
                        _this.yData = []
                        _this.baroption1.param.push(res.jssltbdjt[0].bl1)
                        for(var i = 1;i < res.jssltbdjt.length;i++){
                            _this.xData.push(res.jssltbdjt[i].nf)
                            for(var j = 0;j < res.jssltbdjt[i].tjList.length;j++){
                                _this.baroption1.series[j].data.push(res.jssltbdjt[i].tjList[j].zcount)
                            }
                        }
                        _this.baroption1.xAxis[0].data = _this.xData
                        console.log(_this.baroption1)
                    }
                    if(res.zylist){
                        for(var i = 0;i < res.zylist.length;i++){
