            dataType: "json",
            success: function (res) {
                if (res.code == 401) {
                    location.href = _this.baseUrl01 
                    console.log('判断进入了401，马上开始跳转');  
                }else if (res.code == 403) {
                    console.log('判断进入了403，马上开始跳转');
                    sessionStorage.setItem("login","nop");
                    // location.href = "http://zg.demo.xqit.net/#/p403"
                    
                    _this.$router.push({
                        path: "/p403m",
                        name: "p403m"
                    })
                }else{
                    console.log(res)
                    if(res.jssltbdjt){
                        _this.xData = []
                        _this.yData = []
                        _this.baroption1.param.push(res.jssltbdjt[0].bl1)
                        for(var i = 1;i < res.jssltbdjt.length;i++){
                            _this.xData.push(res.jssltbdjt[i].nf)
                            for(var j = 0;j < res.jssltbdjt[i].tjList.length;j++){
                                _this.baroption1.series[j].data.push(res.jssltbdjt[i].tjList[j].zcount)
                            }
