                        _this.xData = []
                        _this.yData = []
                        _this.baroption2.param.push(res.zyjsbl[0].param1)
                        for(var i = 1;i < res.zyjsbl[0].length;i++){
                            _this.xData.push(res.zyjsbl[0][i].mc)
                            _this.yData.push(res.zyjsbl[0][i].zj.toFixed(2))
                        }
                        _this.baroption2.yAxis[0].name = '(人)'
                        _this.baroption2.xAxis[0].data = _this.xData;
                        _this.baroption2.series[0].data = _this.yData;
                    }
                    if(res.ssxjstbtzy){
                        _this.xData = []
                        _this.yData = []                    
                        _this.baroption3.param.push(res.ssxjstbtzy[0].param1)
                        for(var i = 1;i < res.ssxjstbtzy.length;i++){
                            _this.xData.push(res.ssxjstbtzy[i].mc)
                            _this.yData.push(res.ssxjstbtzy[i].zcount)
                            if(res.ssxjstbtzy[i].dm){
                                _this.baroption3.dm.push(res.ssxjstbtzy[i].dm)
                            }
                        }
                        _this.baroption3.yAxis[0].name = '(%)'
                        _this.baroption3.xAxis[0].data = _this.xData;
                        _this.baroption3.series[0].data = _this.yData;
