                    // if(res.zylist){
                    //     for(var i = 0;i < res.zylist.length;i++){
                    //         // _this.options1[i].key = i+1
                    //         _this.options1[i].value[0] = 'chart1'
                    //         _this.options1[i].value[1] = '0'
                    //         // _this.options1[i].value[2].push(res.zylist[i].mc)
                    //         _this.options1[i].value[2] = res.zylist[i].dm
                    //         _this.options1[i].label = res.zylist[i].mc
                    //     }
                    // }
