<style>
#szdw{
    width: 100%;
    height: 100%;
}
#szdw .row{
    display:flex;
    justify-content:center;
}
#szdw .row .col{
    width:750px;
    margin-top:20px;
}
#szdw .row .cardtitle{
    font-size:25px;
    text-align:center;
    font-weight:900;
    margin-top:-20px;
    margin-bottom:-20px;
}
#szdw .row .coltips{
    min-width:280px;
    max-width:300px;
    margin-top:20px;
}
#szdw .row .coltips2{
    min-width:150px;
    max-width:300px;
    margin-top:20px;
}
#szdw .shaixuan_box{
    position:relative;
    left:13%;
    bottom:32px;
}
@media all and (max-width: 1200px) {
    #szdw .shaixuan_box{
        left: 3%!important;
    }
}
#szdw .row .tipheight{
    height:435px;
}
#szdw .row .tip_select{
    height:465px;
}
#szdw .row .tips{
    font-size:25px;
    text-align:left;
    font-weight:900;
    margin-top:-20px;
    margin-bottom:-20px;
    padding:0 20px;
}
#szdw .row .tippoint{
    margin-top:50px;
    padding:0 20px;
    line-height:40px;
}
#szdw .is-active{
    color: #029348 !important;
}
.el-tabs__active-bar{
    background: #029348 !important;
}
#szdw .is-top :hover{
    color: #029348 !important;
}
#szdw .is-left :hover{
    color: #029348 !important;
}
#szdw .el-card{
    padding: 40px 0 40px 0;
}
.el-tabs__content{
    overflow:inherit;
    padding-bottom: 20px;
}
.el-card{
    transition: 0s;
}
</style>




